import React from 'react'
import Heading from './components/Heading'
import { Box, Button, Container, IconButton, Typography } from '@mui/material'
import PropertyListing from './components/PropertyListing'

export default function PropertyListFilterIndex() {
  return (
   <Box sx={{backgroundColor: "#F7F7F7"}}>
     <Container maxWidth="xl" sx={{ px: { lg: 7, xs: 1 } }} >
      <Heading/>
      <Box
        sx={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          mb: 2,
          // border: "2px solid #D8D8D8",
          borderRadius: "8px",
          px: 4,
          py: 2,
          background: "#fff",
          flexWrap: "wrap",
          gap: 2,
        }}
      >
        <Typography variant="body1">
          Showing 1 â€“ {properties.length} of {properties.length} results
        </Typography>
        <Box
          sx={{
            display: "flex",
            gap: 2,
            alignItems: "center",
            flexWrap: "wrap",
          }}
        >
          <Box sx={{ display: "flex", gap: 2 }}>
            <IconButton
              sx={{
                borderRadius: "8px",
                border: "2px solid #D8D8D8",
                color: "#4D4D4D",
                "&:hover": {
                  background: "#E3E3E3",
                  color: "#4D4D4D",
                },
                height: "42px",
                width: "42px",
              }}
            >
              <GridViewIcon />
            </IconButton>
            <IconButton
              sx={{
                borderRadius: "8px",
                border: "2px solid #D8D8D8",
                color: "#4D4D4D",
                "&:hover": {
                  background: "#E3E3E3",
                  color: "#4D4D4D",
                },
                height: "42px",
                width: "42px",
              }}
            >
              <FormatListBulletedIcon />
            </IconButton>
            <Button
              startIcon={<FavoriteBorderIcon />}
              sx={{
                textTransform: "none",
                borderRadius: "8px",
                color: "#4D4D4D",
                "&:hover": {
                  background: "#E3E3E3",
                  color: "#4D4D4D",
                },
                height: "42px",
              }}
            >
              Save Search
            </Button>
          </Box>
          <Typography>Sort By</Typography>
          <FormControl
            variant="outlined"
            size="small"
            sx={{
              "& .MuiOutlinedInput-notchedOutline": {
                border: "none", // No border around the field
              },
              "& .MuiOutlinedInput-root": {
                paddingRight: "8px", // Adjust padding for the dropdown icon
              },
              "& .MuiSelect-icon": {
                top: "calc(50% - 12px)", // Center align the arrow icon
                right: "8px", // Align icon to the right
              },
              "& .Mui-focused .MuiOutlinedInput-notchedOutline": {
                border: "none", // No border on focus
              },
            }}
          >
            <Select
              value={sort}
              onChange={handleSortChange}
              displayEmpty
              renderValue={(selected) => {
                const selectedOption = menuOptions.find(
                  (option) => option.value === selected
                );
                return selectedOption ? selectedOption.label : "";
              }}
              MenuProps={{
                PaperProps: {
                  sx: {
                    borderRadius: "8px", // Rounded corners for dropdown menu
                    boxShadow: 5, // Subtle shadow
                    marginTop: "30px",
                    "& .MuiMenuItem-root": {
                      padding: "12px 16px",
                    },
                    "& .MuiDivider-root": {
                      margin: "0 16px",
                    },
                  },
                },
              }}
              sx={{
                "& .MuiSelect-select": {
                  padding: "8px 16px",
                  fontSize: "14px",
                  display: "flex",
                  alignItems: "center",
                },
              }}
            >
              {menuItems}
            </Select>
          </FormControl>
        </Box>
      </Box>
      <PropertyListing/>
    </Container>
   </Box>
  )
}
